




































.list-wrapper {
  width: 87%;
  -webkit-transition: all .4s;
  transition: all .4s;
}

.list-container li {
  border-bottom: 1px solid #eee;
}

.list-title {
  font-size: 26px;
  font-weight: 500;
  color: #404040;
  margin-top: 0;
}

.list-abstract {
  font-size: 16px;
  color: #919191;
  font-weight: 100;
}

.list-updated {
  font-family: "Comic Sans MS", curslve, sans-serif;
  font-size: 18px;
  color: #8b8b8b;
  padding:5px 0;
}

.list-container li a {
  padding: 1rem 2rem;
  /*background-color: #;*/
  display: block;
  -webkit-transition: all .3s;
  transition: all .3s;
}

.list-container li a:hover {
  background-color: #ededed;
}

.staggered-transition {
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
  overflow: hidden;
  margin: 0;
}
.staggered-enter, .staggered-leave {
  opacity: 0;
  height: 0;
}




